.buttonGoBack{
    font-size: 16px;
    border-radius: 4px;
    outline: none;
    font-weight: 600;
    padding: 8px 20px;
    text-align: center;
    border: 2px solid #4f46e5;
    transition: .2s;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
    background-color: #4f46e5;

    &:hover{
        background-color: #fff;
        color: #4f46e5;
    }
}